
@use "sass:color";
@use "config" as *;

.search-container {
   position: relative;
   margin-bottom: 30px;
}

.search-input {
   width: 100% !important;
   max-width: none !important;
   padding: 16px 20px;
   margin-bottom: 10px;
   border: 1px solid $silver;
   border-radius: 8px;
   font-size: 18px;
   background-color: $white;
   color: $black;
}

.filters {
   display: flex;
   gap: 10px;
   margin-bottom: 20px;
   flex-wrap: wrap;
}

.filter-select {
   padding: 8px;
   border: 1px solid $silver;
   border-radius: 4px;
   background-color: $white;
   color: $black;
}

.clear-filters-btn {
   padding: 8px 12px;
   background: #f5f5f5; // Light grey for button background
   border: 1px solid $silver;
   border-radius: 4px;
   cursor: pointer;
   color: $black;
}

.search-suggestions-dropdown {
   display: none;
   background-color: $white;
   border: 1px solid $silver;
   border-radius: 4px;
   max-height: 200px;
   overflow-y: auto;
   position: absolute;
   width: 100%;
   z-index: 1000;
}

.suggestion-item {
   padding: 8px 12px;
   cursor: pointer;
   border-radius: 3px;
   color: $black;
}

.suggestion-item:hover {
   background-color: #f0f0f0;
}

.search-status {
   color: $grey;
   font-size: 14px;
   margin-bottom: 10px;
}

.post-item.highlighted {
   background-color: #fff3cd; // Light yellow for highlight
   padding: 5px;
   margin: 2px 0;
   border-radius: 3px;
}

.search-highlight {
   background-color: #ffeb3b; // Yellow for search highlight
   padding: 1px 2px;
   border-radius: 2px;
}



.relevance-score {
   font-size: 0.8em;
   color: $grey;
   margin-left: 10px;
}

.filter-active {
   background-color: $blue !important;
   color: $white !important;
}

.pagination-controls {
   display: none;
   align-items: center;
   justify-content: center;
   gap: 15px;
   margin-top: 20px;
   padding: 10px;
}

.pagination-btn {
   padding: 8px 16px;
   background: $blue;
   color: $white;
   border: none;
   border-radius: 4px;
   cursor: pointer;
   transition: background-color 0.2s;
}

.pagination-btn:hover:not(:disabled) {
   background: #0056b3; // Darker blue on hover
}

.pagination-btn:disabled {
   background: $silver;
   cursor: not-allowed;
}

.page-info {
   font-size: 14px;
   color: $grey;
   min-width: 120px;
   text-align: center;
}

.post-categories {
   color: $grey;
   font-size: 0.9em;
}

@media (prefers-color-scheme: dark) {
   .search-input {
      background-color: #2a2d33;
      color: $white;
      border-color: $grey;
   }
   
   .search-input::placeholder {
      color: #999;
   }

   .filter-select {
      background-color: $dark-bg;
      color: $white;
      border-color: $grey;
   }

   .clear-filters-btn {
      background: $grey;
      border-color: $grey;
      color: $white;
   }

   .search-suggestions-dropdown {
      background-color: $dark-bg;
      border-color: $grey;
      color: $white;
   }

   .search-status {
      color: $silver;
   }

   .post-item.highlighted {
      background-color: color.adjust($dark-bg, $lightness: 10%); // Slightly lighter dark background
   }

   .search-highlight {
      background-color: #8B8000; // Darker yellow for highlight
      color: $white;
   }

   .suggestion-item {
      color: $white;
   }

   .suggestion-item:hover {
      background-color: color.adjust($dark-bg, $lightness: 15%);
   }

   .relevance-score {
      color: $silver;
   }

   .pagination-btn {
      background: $blue;
      color: $white;
   }

   .pagination-btn:hover:not(:disabled) {
      background: color.adjust($blue, $lightness: -10%);
   }

   .pagination-btn:disabled {
      background: $grey;
   }

   .page-info {
      color: $silver;
   }

   .post-categories {
      color: $silver;
   }
}
