@use "sass:color";

/***************
COLOR VARIABLES
***************/

// Better default colors
// Cool
$aqua: #7FDBFF;
$blue: #0074D9;
$navy: #001F3F;
$teal: #39CCCC;
$green: #2ECC40;
$olive: #3D9970;
$lime: #01FF70;
// Warm
$yellow: #FFDC00;
$orange: #FF851B;
$red: #FF4136;
$fuchsia: #F012BE;
$purple: #B10DC9;
$maroon: #85144B;
// Gray Scale
$white: #fff;
$silver: #ddd;
$grey: #aaa;
$black: #111;
$dark-bg: #14161a;

// Color variables
$primary-color: #5a7400;
$secondary-color: #233E5B;
$tertiary-color: #aaa;
$link-color: $primary-color;
$background-color: $primary-color;

$bad-color: $red; // Bad color for errors, validation etc.
$good-color: $green; // Good color for success etc.
$info-color: $blue; // Good color for success etc.
$warning-color: $orange; // Good color for success etc.
$subtle-color: $grey; // Subtle color for subtle text


/***************
CONSISTENT SETTINGS
***************/

$br: 5px; // Border-radius
$btn-br: 5px; // Button border-radius
$padding: 40px; // Padding and margins
$line-height: 44px; // Consistent height for buttons, inputs etc.


/***************
TYPE SETTINGS
***************/

$sans-serif: "Lato", Helvetica, Arial, sans-serif;
$serif: "Spectral", Georgia, Times, serif;